import * as React from "react";
import Box from "@mui/material/Box";
import {Airforce} from "../../importsStyledImages/importsStyledImages";
import {WomenTypography} from "../../importsTypography/importsTypography";
const AvailableWomen = ({children}) => {
  return (
    <Box
    sx={{
      backgroundColor: "#2A5D67",
      borderRadius: "20px",
      padding: "10px",
      height: "300px",
      width: "220px",
      marginLeft:"33px"
    }}
  >
    <Airforce height= "118px"
        width= "210px"
        marginLeft= "8px"
        marginTop= "25px"/>
    <WomenTypography/>
    {children}
    </Box>
  );
};
export default AvailableWomen;